:host {
  display: block;
}

alc-popup {
  @apply relative inline-block;
}

.alc-popup__content {
  @apply w-max absolute top-0 left-0 hidden z-[1060];
}

.alc-popup__content--active {
  @apply block;
}

.alc-popup__content--fixed {
  @apply fixed;
}

.alc-popup__arrow {
  /*
    Sendo o componente alc-popup um utilitÃ¡rio para elementos flutuantes,
    definindo posicionamento, distÃ¢ncia, deslocamento, mas nÃ£o cor, borda etc.,
    quem for usar o alc-popup Ã© que tem que definir esses aspectos visuais.
    Para a seta (arrow), define-se portanto somente o posicionamento.
  */
  @apply absolute;
}
